export default {
	data() {
		return {
			more: false,
			pageNum: 1,
			pageSize: 10,
			loading: true,
			list: []
		};
	},
	onLoad() {
		this.getList();
	},
	onReachBottom() {
		this.getList();
	},
	methods: {
		getList() {
			if (this.loading) {
				this.loading = false;
				this.$Fetch({
					url: 'Attention/getData',
					data: { pageNum: this.pageNum ,pageSize:this.pageSize},
					debug:true,
					success: res => {
						if (res.length > 0) {
							this.list = this.list.concat(res);
							if (res.length === this.pageSize) {
								this.pageNum++;
								this.loading = true;
								this.more = true;
							} else {
								this.more = false;
							}
						}
					}
				});
			}
		}
	}
};
